import React from 'react'
import { useState } from "react"
import { useCookies } from 'react-cookie'

const Model = ({ mode, setShowModal, getData, task }) => {
  const [cookies, setCookie, removeCookie] = useCookies(null)
  const [error, setError] = useState(null)
  const editMode = mode === 'edit' ? true : false

  const [data, setData] = useState({
    user_email: editMode ? task.user_email : cookies.Email,
    title: editMode ? task.title : "",
    progress: editMode ? task.progress : 50,
    date: editMode ? task.date : new Date()
  })

  const postData = async (e) => {
    e.preventDefault()
    try {
      const response = await fetch(`${process.env.REACT_APP_SERVERURL}/to-do/todos`, {
        method: "POST",
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify(data)
      })
      const responseJSON = await response.json()
        if (response.ok) {
          setShowModal(false)
          getData()
        } else {
          console.log(responseJSON)
          setError(responseJSON.message)
        }
    } catch (error) {
      console.error(error)
    }
  }

  const editData = async (e) => {
    e.preventDefault()
    try {
      const response = await fetch(`${process.env.REACT_APP_SERVERURL}/to-do/todos/${task.id}`, {
        method: 'PUT',
        headers: {'content-type': 'application/json'},
        body: JSON.stringify(data)
      })
      const responseJSON = await response.json()
      if (response.ok) {
        setShowModal(false)
        getData()
      } else {
        console.log(responseJSON.message)
        setError(responseJSON.message)
      }
    } catch (error) {
      console.error(error)
    }
  }

  const handleChange = (e) => {
    const {name, value} = e.target;
    setData(data => ({
      ...data, 
      [name] : value
    }))
  }

    return (
      <div className="overlay">
        <div className="modal">
          <div className="form-title-container">
            <h3>Lets {mode} your task</h3>
            <button onClick={() => setShowModal(false)}>X</button>
          </div>
          <form>
            <input 
            required 
            maxLength={30} 
            placeholder=" Your task goes here"
            name="title"
            value={data.title}
            onChange={handleChange}
            />
            <br/>
            <input
            required
            type="range"
            min='0'
            max='100'
            name="progress"
            value={data.progress}
            onChange={handleChange}
            />
            <input className={mode} type="submit" onClick={editMode ? editData : postData}
            />
          </form>
          <div className="error-container">
            {error && 
            <p className="display-auth-error">
              {error}  
            </p>}
          </div>
        </div>
      </div>
    );
  }
  
  export default Model;
  